<template>
  <canvas v-el:chart width="100%" height="100%"></canvas>
</template>
<script>
  import Chart from 'chart.js'

  export default {
    props: ['chartData'],
    data () {
      return {}
    },
    ready () {
      var ctx = this.$els.chart.getContext('2d')
      this.myLineChart = new Chart(ctx).Line(this.chartData, { responsive: true })
    }
  }
</script>



